/*
 * jekyll-highlight-cards Default Styles
 *
 * These styles provide a sensible default appearance for linkcard and polaroid tags.
 * You can override these styles in your Jekyll site by:
 * 1. Not importing this file (fully custom CSS)
 * 2. Importing and overriding specific classes
 * 3. Using higher specificity selectors
 *
 * To use these styles, add to your site's _config.yml or main SCSS:
 *   @use "highlight-cards";
 *
 * All styles use low specificity for easy customization.
 */

/* ============================================================================
   Linkcard Styles
   ========================================================================= */

.link-card {
  padding: 1em 1.25em;
  text-align: center;
  position: relative;
}

.link-card-archive {
  position: absolute;
  right: 0.75rem;
  bottom: 0.5rem;
}

/* ============================================================================
   Polaroid Styles
   ========================================================================= */


.polaroid-container {
  display: block;
  width: 100%;
  text-align: center;
}

.polaroid {
  display: inline-block;
  position: relative;
  border: 1px solid #333;
  padding: 10px 10px 25px 10px;
  margin: 1em;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  text-align: center;
  max-width: 100%;
  transition: box-shadow 0.2s ease, transform 0.2s ease;

  .polaroid-image {
    display: block;
    max-width: 100%;
    border: 1px solid #333;
    margin: 0 auto;
  }

  .polaroid-title {
    margin-top: 5px;
    padding: 0 10px;
  }

  .polaroid-link {
    margin-top: 5px;
    padding: 0 10px;
    word-break: break-all;
  }

  .polaroid-archive {
    position: absolute;
    right: 0.75rem;
    bottom: 0.5rem;
  }
}

/* ============================================================================
   Print Styles
   ========================================================================= */

@media print {
  .link-card,
  .polaroid {
    box-shadow: none;
    border: 1px solid #333;
    page-break-inside: avoid;
  }
}

